*{
  padding:0;
  margin:0;
}

.chatBox{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mainChat{
  display: flex;
  flex-direction: column;
  border: thin solid black;
  height:15cm;
  width:80vw;
}

.userImg{
  width:1cm;
  height: 1cm;
}

.nameDiv{
  background-color: rgb(147, 234, 226);
  width:fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}
.userName{
  font-size: 12px;
  color:rgb(91, 91, 91);
}
.leftMsg{
  display: flex;
  flex-direction: row;
  column-gap:0.2cm;
  margin-top: 0.2cm;
}
.left{
  text-align: left;
  /* padding:0.5cm; */
}

.right{
  text-align: right;
  background-color: rgb(238, 242, 159);
  width:fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}

.rightMsg{
  margin: 0.2cm;
display: flex;
flex-direction: row;
justify-content: flex-end;
 
  
}